
  .todo-list {
    background: #e8e8e8;
    border-radius: 4px;
    padding: 5px;
  }
  
  .todo {
    background: #fff;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    padding: 3px 10px;
    font-size: 12px;
    margin-bottom: 6px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .todo-input {
    width: 100%;
  }

  .table-cell {
    vertical-align: top;
  }